import Vue from 'vue';
import Component from 'vue-class-component';
import checkbox from 'muse-ui/checkbox';
import router from '~/router';
import { Action, State } from 'vuex-class';

import Utils from '~/utils';
import { IAcquaintanceTaskParams } from '~/entity/route_params';
import { CACHE_NAME } from '~/cache';
import { ToastMsg } from '~/entity/toast';

@Component({
    components: {
        muCheckbox: checkbox
    }
})
export default class TaskSelectComp extends Vue {
    isAccepctProtocol: boolean = true;

    @State(state => state.user.friends)
    friendsList;
    @State(state => state.user.roles)
    userRoles;
    @State(state => state.user.user)
    userInfo;

    @State(state => state.acquaintanceState.acquaintanceList)
    acquaintanceList;

    @Action('resetFriendList')
    resetFriendList;

    @Action('resetSelectedAcquaintanceList')
    resetSelectedAcquaintanceList;   
    
    @Action('showToast')
    showToast;

    enterSelectFriends() {
        this.resetFriendList(Utils.cloneObj(this.friendsList));
        console.log(this.acquaintanceList);
        this.resetSelectedAcquaintanceList([]);
        const params: IAcquaintanceTaskParams = {
            action: 'fromFindObject',
            id: 'me'
        };
        this['$router'].push({
            name: 'acquaintanceTask',
            params
        });              
    }

    enterSetOpenTask() {
        if (this.userRoles.indexOf('authorized_user') !== -1) { // 判断用户角色,是否已认证用户
            const initCompanys = [];
            Utils.saveItem(CACHE_NAME.addCompanyInOpenTask, initCompanys);
            this['$router'].push({
                name: 'openTask'
            }); 
        } else {
            this.showToast(new ToastMsg('您未认证，暂时无法发布公开任务，请先完成认证'));
            this['$router'].push({
                name: 'authIndex'
            });
        }
    }
    enterSystemTask() {
        if (this.userRoles.indexOf('authorized_user') !== -1) { // 判断用户角色,是否已认证用户
            Utils.saveItem(CACHE_NAME.criteriaVoCache, Utils.cloneObj( this.userInfo.criteriaVo));
            this['$router'].push({
                name: 'systemTask'
            }); 
        } else {
            this.showToast(new ToastMsg('您未认证，暂时无法发布公开任务，请先完成认证'));
            this['$router'].push({
                name: 'authIndex'
            });
        }
    }  
}



